
<template>
  <el-container>
    <el-header style="text-align: right; font-size: 12px"
      ><el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
      >
        <el-menu-item index="1"
          ><div @click="handleMenuCollapse" class="toggle-icon">
            <i
              :class="collapsed ? 'el-icon-s-unfold' : 'el-icon-s-fold'"
            ></i></div
        ></el-menu-item>

        <el-menu-item index="2"
          ><router-link to="/">Hello</router-link></el-menu-item
        >
        <el-menu-item index="3"
          ><router-link to="/callservice">Service</router-link></el-menu-item
        >
        <el-menu-item v-if="showAdminBoard" index="4"
          ><router-link to="/admin">Admin Board</router-link></el-menu-item
        >
        <el-menu-item v-if="showModeratorBoard" index="5"
          ><router-link to="/mod">Moderator Board</router-link></el-menu-item
        >
        <el-menu-item v-if="currentUser" index="6"
          ><router-link to="/user">User</router-link></el-menu-item
        >

        <div v-if="!currentUser" class="navbar-nav ml-auto">
          <el-menu-item index="4"
            ><router-link to="/register">Register</router-link></el-menu-item
          >
          <el-menu-item index="5"
            ><router-link to="/login">Login</router-link></el-menu-item
          >
        </div>
        <div v-if="currentUser" class="navbar-nav ml-auto">
          <el-menu-item index="4"
            ><router-link to="/profile">
              {{ currentUser.username }}</router-link
            ></el-menu-item
          >
          <el-menu-item index="5"
            ><a @click.prevent="logOut">LogOut</a></el-menu-item
          >
        </div>
      </el-menu>
    </el-header>

    <el-main>
      <img src="../assets/logo.png" alt="" />
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "TopBar",
  props: {
    collapsed: Boolean,
  },
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
    };
  },
  methods: {
    handleMenuCollapse() {
      this.$emit("handleMenuCollapse");
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    logOut() {
      this.$store.dispatch("auth/logout");
      this.$router.push("/login");
    },
  },
  currentUser() {
    return this.$store.state.auth.user;
  },
  showAdminBoard() {
    if (this.currentUser && this.currentUser["roles"]) {
      return this.currentUser["roles"].includes("ROLE_ADMIN");
    }

    return false;
  },
  showModeratorBoard() {
    if (this.currentUser && this.currentUser["roles"]) {
      return this.currentUser["roles"].includes("ROLE_MODERATOR");
    }

    return false;
  },
  emits: ["handleMenuCollapse"],
};
</script>

<style scoped>
.el-aside {
  color: #333;
}
.side-menu {
  width: 200px;
}
.el-header {
  color: #333;
  line-height: 60px;
}

.el-menu--collapse {
  width: 60px;
}
.el-menu-item * {
  text-decoration: none;
}
</style>

