<template>
  <div class="container">
    <SideBar />
    <header class="jumbotron">
      <h3>{{ content }}</h3>
    </header>
  </div>
</template>

<script>
import UserService from "../api/user-service";
import SideBar from "./SideBar";
export default {
  name: "User",
  components: {
    SideBar,
  },
  data() {
    return {
      content: "",
    };
  },
  mounted() {
    UserService.getAdminBoard().then(
      (response) => {
        console.log(response.data);

        this.content = response.data;
        console.log(response.data);
      },
      (error) => {
        this.content =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();
      }
    );
  },
};
</script>